.cart-icon {
  position: relative;
  display: inline-block;

  > .ion {
    color: $green-dark;
    font-size: 2.25rem;
    font-weight: 500;
  }

  > .count {
    display: inline-block;

    width: 24px;
    height: 24px;
    background-color: #f18703;
    border-radius: 50%;
    overflow: hidden;

    color: white;
    font-size: 0.75rem;
    font-weight: 700;
    line-height: 24px;
    text-align: center;

    position: absolute;
    right: -15px;
    top: -10px;
  }
}

.cart-text {
  display: inline-block;
  > .header {
    display: block;
    color: #3b4854;
    font-size: 0.9375rem;
    font-weight: 700;
    margin-bottom: 3px;
  }

  > .text {
    display: block;
    color: #2a3641;
    font-size: 0.9375rem;
    font-weight: 400;
    white-space: nowrap;
  }
}

/*** HEADER ***/
#header {
  background: white;
  color: $gray;
  #_desktop_logo h1 {
    margin: 0;
  }
  .logo {
    max-width: 100%;
  }
  a {
    color: $gray-darker;
    &:hover {
      text-decoration: none;
      color: $brand-primary;
    }
  }
  .menu {
    > ul {
      margin: 0;
      @include media-breakpoint-up(lg) {
        display: flex;
        justify-content: space-between;
      }
      > li {
        display: inline-block;
      }
    }
  }
  .header-nav {
    padding-top: 10px;
    padding-bottom: 10px;
    #menu-icon {
      vertical-align: middle;
      cursor: pointer;
      margin: 0 1rem;
      .material-icons {
        line-height: 50px;
      }
    }
    .center-nav {
      display: flex;
      align-items: center;
    }
    .right-nav {
      display: flex;
      flex-wrap: nowrap;
      justify-content: flex-end;
    }
    .currency-selector {
      margin-top: 0.9375rem;
      margin-left: 0.9375rem;
      white-space: nowrap;
    }
    .user-info {
      text-align: right;
      white-space: nowrap;
      @include media-breakpoint-up(lg) {
        float: right;
      }
      a {
        @include media-breakpoint-up(lg) {
          display: flex;
          align-items: center;
        }
        display: inline-block;
        color: $gray-dark;
        font-size: 0.875rem;
        .ion {
          font-weight: 500;
          font-size: 1.875rem;
          color: $green-dark;
          margin-right: 10px;
        }
      }
      .account {
        margin-left: $small-space;
      }
    }
    .language-selector {
      margin-top: 0.9375rem;
      white-space: nowrap;
    }
    #_desktop_cart {
      max-width: 100%;
      .cart-text {
        overflow: hidden;
      }
    }
    .blockcart {
      .cart-icon {
        margin-right: 30px;
      }
      .wrapper {
        @include media-breakpoint-up(lg) {
          display: flex;
          align-items: center;
        }
      }
    }
    #_desktop_contact_link{
      @include media-breakpoint-up(lg) {
        padding-left: 45px;
        padding-right: 40px;
      }
      display: inline-block;
      #contact-link {
        @include media-breakpoint-up(lg) {
          display: flex;
          align-items: center;
        }
        font-size: 0.875rem;
        color: $gray-darker;
        .number {
          display: inline-block;
          padding-left: 10px;
        }
        .ion {
          font-weight: 500;
          font-size: 1.875rem;
          color: $green-dark;
        }
      }
    }
    #_desktop_user_info {
      flex-grow: 1;
    }
    .material-icons {
      line-height: inherit;
      &.expand-more {
        margin-left: -0.375rem;
      }
    }
  }
  .header-top {
    padding-bottom: 1.25rem;
    padding-top: 1.25rem;
    box-shadow: 0 0 18px 0 rgba(10, 10, 10, 0.07);
    > .container {
      position: relative;
    }
    .position-static {
      position: static;
    }
    a[data-depth="0"] {
      color: $gray;
      text-transform: uppercase;
    }
    .search-widget {
      float: right;
    }
  }
  .top-menu-link {
    margin-left: $medium-space;
  }
  .top-menu {
    > li > a {
      font-family: 'oswald';
      font-size: 1.125rem;
    }
  }
}

.popover {
  font-family: inherit;
}
/*** WRAPPER ***/
#wrapper {
  padding-top: 1.563rem;
  .banner {
    margin-bottom: 1.5rem;
    display: block;
    img {
      box-shadow: 1px 1px 7px 0 rgba(0, 0, 0, 0.15);
    }
  }
  .breadcrumb {
    background: transparent;
    padding: 0;
    &[data-depth="1"] {
      display: none;
    }
    ol {
      padding-left: 0;
      margin-bottom: 0;
    }
    li {
      display: inline;
      &::after {
        content: "/";
        color: $gray;
        margin: 0.3125rem;
      }
      &:last-child {
        content: "/";
        color: $gray;
        margin: 0;
        &::after {
          content: "";
        }
      }
      a {
        color: $gray-darker;
      }
    }
  }
}
/*** MAIN ***/
#main {
  .page-header {
    margin-bottom: 1.563rem;
  }
  .page-content {
    margin-bottom: 1.563rem;
    h6 {
      margin-bottom: 1.125rem;
    }
    #notifications {
      margin-left: -15px;
      margin-right: -15px;
    }
  }
  .page-footer {
    margin-bottom: 1.563rem;
  }
}
#notifications {
  ul {
    margin-bottom: 0;
  }
}
/*** FOOTER ***/
#footer {
  padding-top: 2.5rem;
}

@include media-breakpoint-up(lg) {
  #header .header-nav .container .row {
    display: flex;
    align-items: center;
  }
}

/*** Responsive part ***/
@include media-breakpoint-down(md) {
  .container {
    max-width: 100%;
  }
  #products .product-miniature,
  .featured-products .product-miniature {
    margin: 0 auto;
  }
  .sub-menu {
    left: 0;
    min-width: 100%;
  }
  #blockcart-modal .product-image {
    width: 100%;
    display: block;
    max-width: 15.63rem;
    margin: 0 auto 0.9375rem;
  }
  #blockcart-modal .cart-content {
    padding-left: 0;
  }
  #blockcart-modal .product-name,
  #product-availability {
    margin-top: $small-space;
  }
  #search_filters .facet .facet-label {
    text-align: left;
  }
  .block-category .category-cover {
    position: relative;
    text-align: center;
  }
  .block-category {
    padding-bottom: 0;
  }
}
@include media-breakpoint-down(sm) {
  #wrapper {
    box-shadow: none;
  }
  #checkout-cart-summary {
    float: none;
    width: 100%;
    margin-top: 1rem;
  }
  #header {
  }
  section.checkout-step {
    width: 100%;
  }
  .default-input {
    min-width: 100%;
  }
  label {
    clear: both;
  }
  #products .product-miniature,
  .featured-products .product-miniature {
    margin: 0 auto;
  }
  .block-contact {
    padding-left: 0.9375rem;
    border: none;
  }
  .menu,
  .dropdown-item {
    padding-left: 0;
  }
  #footer {
    padding-top: 0.5rem;
  }
}

@include media-breakpoint-down(xs) {
  #left-column, #content-wrapper {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

input[type=number] {
  -moz-appearance:textfield;
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}
